
.login {
  width: 100%;
  background: #456;
}
.login h1 {
  font-size: 20px;
  line-height: 3;
  color: #fff;
  text-align: center;
}
.login-card {
  margin: 0 auto;
  font-size: 16px;
}
.login-header,
.login-card p {
  margin-top: 0;
  margin-bottom: 0;
}

/* The triangle form is achieved by a CSS hack */
.login-header {
  background: #28d;
  padding: 20px;
  font-size: 1.4em;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}
.login-container {
  background: #ebebeb;
  padding: 12px;
}

/* Every row inside .login-container is defined with p tags */
.login-card p {
  padding: 12px;
}
.login-card input, .login-card .submit {
  box-sizing: border-box;
  display: block;
  width: 100%;
  border-width: 1px;
  border-style: solid;
  padding: 16px;
  outline: 0;
  font-family: inherit;
  font-size: 0.95em;
}
.login-card input[type="text"],
.login-card input[type="password"] {
  background: #fff;
  border-color: #bbb;
  color: #555;
}

/* Text fields' focus effect */
.login-card input[type="text"]:focus,
.login-card input[type="password"]:focus {
  border-color: #888;
}
.login-card .submit {
  text-align: center;
  background: #28d;
  border-color: transparent;
  color: #fff;
  cursor: pointer;
}
.login-card .submit:hover {
  background: #17c;
}

/* Buttons' focus effect */
.login-card .submit:focus {
  border-color: #05a;
}



