
.content.images {
  background: #eee;
  border: 1px solid #bfcbd9;
}
.content textarea {
  outline: none;
}
.bar {
  background: #fff;
  padding: 10px 0px;
}
.bar>i {
  font-size: 20px;
  color: #2eabc6;
}
.image-btn {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: 8px;
}
.img-list span{
  display: block;
  text-align: center;
  border: 1px solid gray;
  padding: 6px 0;
  margin: 8px 15px;
  border-radius: 6px;
}
.add-image-btn label {
  display: inline-block;
  width: 80px;
  height: 80px;
  text-align: center;
  border: 1px solid #bfcbd9;
  margin: 8px;
}
.add-image-btn input[type="file"] {
  position: absolute;
  left: -9999px;
}
.add-image-btn label::before {
  content: '+';
  line-height: 70px;
  font-size: 30px;
  color: #999;
}



